<template>
  <section class="end-customer">
    <!-- title -->
    <h1 class="end-customer__title page__title">Конечный заказчик</h1>
    <!-- content -->
    <div class="end-customer-content page-wrapper">
      <div class="end-customer-fields">
        <div class="end-customer-fields-row fields-row">
          <base-input
            :id="'end-customer-customer'"
            :label="'Конечный заказчик'"
            v-model="endCustomer"
          />
        </div>
        <div class="end-customer-fields-row fields-row _inn">
          <base-input :label="'ИНН'" :id="'end-customer-inn'" v-model="inn" />
        </div>
        <div class="end-customer-fields-row fields-row">
          <base-input
            :label="'Регион'"
            :id="'end-customer-region'"
            v-model="region"
          />
        </div>
        <div class="end-customer-fields-row fields-row _city">
          <base-input
            :label="'Город'"
            :id="'end-customer-city'"
            v-model="city"
          />
        </div>
        <div class="end-customer-fields-row fields-row _bio">
          <base-input
            :label="'ФИО заведующего эндоскопии'"
            :id="'end-customer'"
            :placeholder="'Если есть'"
            :labelStyle="{ transform: `translateY(10px)` }"
            v-model="bio"
          />
        </div>
      </div>
    </div>
    <!-- buttons -->
    <div class="end-customer-buttons page-buttons _save">
      <div class="end-customer-buttons__send _save__button">
        <base-button :type="'filled'" @clicked="send">Сохранить</base-button>
      </div>
    </div>
    <!-- search -->
    <div class="end-customer-search page-wrapper">
      <div class="end-customer-search-field">
        <base-input
          :id="'endCustomerSearch'"
          :placeholder="!$isMobile ? 'Поиск по контрагенту или ИНН' : 'Поиск'"
          :search="true"
          :searchIcon="true"
          :suggestionWidth="380"
          :suggestions="suggestions"
          @setSuggestion="(value) => (customers = value)"
          @clear="customers = ''"
          v-model="customers"
        />
      </div>
    </div>
    <!-- table -->
    <div class="page-wrapper">
      <end-customer-table :endCustomers="endCustomers" />
    </div>
  </section>
  <base-modal
    :open="openModal"
    :text="'Отправлено'"
    @close="openModal = false"
  />
</template>

<script>
import BaseInput from "@/components/common/Base.Input.vue";
import BaseButton from "@/components/common/Base.Button.vue";
import BaseModal from "@/components/common/Base.Modal.vue";
import EndCustomerTable from "@/components/endCustomer/EndCustomerTable";

export default {
  name: "EndCustomer",
  components: {
    BaseInput,
    BaseButton,
    BaseModal,
    EndCustomerTable,
  },
  data() {
    return {
      endCustomer: "",
      inn: "",
      region: "",
      city: "",
      bio: "",
      customers: "",
      openModal: false,
      endCustomers: [],
      suggestions: [
        "8/21-ES",
        "7/21-ES",
        "6/21-ES",
        "5/21-ES",
        "5/21-ES",
        "5/21-ES",
        "5/21-ES",
        "5/21-ES",
      ],
    };
  },
  mounted() {
    this.getEndCustomers();
  },
  methods: {
    send() {
      this.openModal = true;
      console.log("send");
    },
    getEndCustomers() {
      this.$api.get(`${process.env.VUE_APP_API}/end-customer`).then((res) => {
        for (let c of res.data.results) {
          const obj = {
            id: c.id,
            name: c.name,
            inn: c.inn,
            region: c.region,
            city: c.city,
            bio: c.head_of_endoscopy_full_name,
          };
          this.endCustomers.push(obj);
        }
      });
    },
  },
};
</script>

