  padding-bottom: 70px;
  &-fields {
    &-row {
      &._inn,
      &._city {
        margin-bottom: 47px;
      }
    }
  }
  &-buttons {
    margin-bottom: 67px;
  }
  &-search {
    margin-bottom: 35px;
